import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Route, Link as RouterLink, Routes, useParams } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import { AppBar, Toolbar, Typography, Drawer, List, ListItem, ListItemText, Container, CssBaseline, Box } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme();

const MarkdownPage = () => {
  const [content, setContent] = useState('');
  const { page } = useParams();

  useEffect(() => {
    const fetchContent = async () => {
      try {
        const response = await fetch(`https://api.github.com/repos/amo-dta/democsm/contents/${page || 'home'}.md`);
        const data = await response.json();
        const decodedContent = atob(data.content);
        setContent(decodedContent);
      } catch (error) {
        console.error('Error fetching content:', error);
        setContent('# Error\nFailed to load content. Please try again later.');
      }
    };

    fetchContent();
  }, [page]);

  return (
    <Container maxWidth="md" sx={{ mt: 4 }}>
      <ReactMarkdown>{content}</ReactMarkdown>
    </Container>
  );
};

const App = () => {
  const [pages, setPages] = useState([]);

  useEffect(() => {
    const fetchPages = async () => {
      try {
        const response = await fetch('https://api.github.com/repos/amo-dta/democsm/contents/');
        const data = await response.json();
        const markdownFiles = data.filter(file => file.name.endsWith('.md'));
        setPages(markdownFiles.map(file => file.name.replace('.md', '')));
      } catch (error) {
        console.error('Error fetching pages:', error);
      }
    };

    fetchPages();
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Box sx={{ display: 'flex' }}>
          <AppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}>
            <Toolbar>
              <Typography variant="h6" noWrap component="div">
                Your Site Name
              </Typography>
            </Toolbar>
          </AppBar>
          <Drawer
            variant="permanent"
            sx={{
              width: 240,
              flexShrink: 0,
              [`& .MuiDrawer-paper`]: { width: 240, boxSizing: 'border-box' },
            }}
          >
            <Toolbar />
            <Box sx={{ overflow: 'auto' }}>
              <List>
                <ListItem button key="home" component={RouterLink} to="/">
                  <ListItemText primary="Home" />
                </ListItem>
                {pages.map((page) => (
                  <ListItem button key={page} component={RouterLink} to={`/${page}`}>
                    <ListItemText primary={page.replace('-', ' ')} />
                  </ListItem>
                ))}
              </List>
            </Box>
          </Drawer>
          <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
            <Toolbar />
            <Routes>
              <Route path="/" element={<MarkdownPage />} />
              <Route path="/:page" element={<MarkdownPage />} />
            </Routes>
          </Box>
        </Box>
      </Router>
    </ThemeProvider>
  );
};

export default App;